<template>
    <header>
        <h1>{{ title }}</h1>
        <Button v-show="homePage"
        @btn-click="$emit('toggle-add-task')"
        :text="showAddTask ? 'Close' : 'Add Task'" 
        :color="showAddTask ? 'red' : 'green'"/>
    </header>
</template>

<script>
import Button from './Button';

export default {
    name: 'Header',
    components: {
        Button,
    },
    computed:{
        homePage(){
            if(this.$route.path === "/"){
                return true;
            } else{
                return false;
            }
        }
    },
    props: {
        title: String,
        showAddTask: Boolean
    },
    
}
</script>

<style scope>
    header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }
</style>